.body-main {
  font-family: "oswald", "Arial", sans-serif;
  margin: 0;
  height: 100%;
}
body {
  margin: 0;
}
.nojs .page-header__toggle {
  display: none;
}
.nojs .coop__map iframe {
  display: none;
}
.nojs .coop__map .product-catalog__title::before {
  display: block;
}
.nojs .coop__map-nojs {
  display: block;
}
@media (max-width: 767px) {
  .nojs .main-nav {
    display: block;
  }
  .nojs .main-nav__list {
    position: static;
  }
}
.wrapper {
  min-height: 100%;
  display: flex;
  flex-direction: column;
}
main {
  display: flex;
  flex-direction: column;
  height: 100%;
  flex-grow: 1;
}
.page-header {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  box-sizing: border-box;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .page-header {
    min-width: 768px;
    flex-direction: row;
    padding: 0 15px 35px 30px;
  }
}
@media (min-width: 1440px) {
  .page-header {
    min-width: 1440px;
    padding: 52px 110px 65px;
  }
}
.page-header__toggle {
  position: absolute;
  content: "";
  border: none;
  background-color: var(--basicWhiteColor);
  width: 64px;
  height: 58px;
  top: 0;
  right: 0;
  padding: 0;
}
.page-header__toggle:hover {
  opacity: 0.8;
}
.page-header__toggle:active {
  opacity: 0.6;
}
@media (min-width: 768px) {
  .page-header__toggle {
    display: none;
  }
}
@media (min-width: 1440px) {
  .page-header__toggle {
    display: none;
  }
}
.page-header__toggle::before {
  position: absolute;
  content: "";
  top: 23px;
  right: 20px;
  width: 24px;
  height: 1.5px;
  background-color: var(--basicBlackColor);
  box-shadow: 0 6.5px 0 0 var(--basicBlackColor), 0 13px 0 0 var(--basicBlackColor);
}
@media (max-width: 767px) {
  .page-footer {
    background-color: var(--primaryBackgrColor);
  }
}
@media (min-width: 768px) {
  .page-footer {
    background-color: var(--primaryBackgrColor);
  }
}
.page-footer--index {
  background-color: var(--basicWhiteColor);
}
.page-footer__logo {
  padding: 39px 0 32px;
  position: relative;
  display: flex;
  justify-content: center;
  width: 100%;
}
.page-footer__logo:hover {
  opacity: 0.6;
}
.page-footer__logo:active {
  opacity: 0.3;
}
@media (max-width: 767px) {
  .page-footer__logo {
    padding: 39px 0 15px;
